@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;800&display=swap";
body {
  height: 100vh;
  width: 100%;
  grid-template-columns: repeat(20, 1fr);
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  display: grid;
  overflow: hidden;
}

.left {
  box-sizing: border-box;
  z-index: 100;
  flex-direction: column;
  grid-column: 1 / 5;
  display: flex;
  position: relative;
  box-shadow: 3px 0 10px #0003;
}

.left .title {
  margin: 0;
  padding: 10px 20px;
  font-size: 25px;
  font-weight: 800;
}

.left .math {
  position: relative;
}

.left .math input {
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #000;
  border-radius: 0;
  outline: 0;
  padding: 10px 5px;
  font-family: Montserrat, sans-serif;
  font-weight: 800;
  display: block;
}

.left .math .remove {
  width: 15px;
  height: 4px;
  cursor: pointer;
  background-color: #000;
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.left .addFunc {
  cursor: pointer;
  z-index: 100;
  width: 15px;
  height: 15px;
  position: absolute;
  bottom: 15px;
  right: 10px;
}

.left .addFunc:before, .left .addFunc:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #000;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.left .addFunc:before {
  transform: translate(-50%, -50%)rotate(90deg);
}

.right {
  grid-column: 5 / -1;
}

.right canvas {
  width: 100%;
  height: 100%;
}

